@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
:root{--font: 'Inter', sans-serif;--black: #000000;--white: #ffffff;--dark-gray: #303030}body{margin:0;padding:0;box-sizing:border-box;font-family:'Inter', sans-serif;font-family:var(--font);font-weight:400;font-size:18px;line-height:28px;color:#ffffff;color:var(--white)}h1,h2,h3,h4,h5,h6{font-family:'Inter', sans-serif;font-family:var(--font);margin:0}h1{font-weight:700;font-size:200px;line-height:200px;letter-spacing:-5px}h2{font-weight:700;font-size:56px;line-height:68px}h3{font-weight:700;font-size:24px;line-height:28px;letter-spacing:-4.8px;text-transform:uppercase}h4{font-weight:400;font-size:20px;line-height:28px;letter-spacing:4px;text-transform:uppercase}h5{font-weight:700;font-size:18px;line-height:28px}h6{font-weight:700;font-size:15px;line-height:28px;letter-spacing:3px}

.app{height:100vh;display:flex;box-sizing:border-box;flex-direction:column;background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-daytime.13c049fc.jpg);background-repeat:no-repeat;background-position:center;background-size:100% 100%}.app--open .clock{height:60vh;justify-content:flex-end}.app--open .btn--primary>span{transform:rotate(360deg)}.app--open .quote{display:none}.app--open .info{display:flex;flex-direction:column;height:40vh;background:rgba(255,255,255,0.75);-webkit-backdrop-filter:blur(40.7742px);backdrop-filter:blur(40.7742px);color:var(--black);padding:48px 26px 0 26px}.app--open .info__detail{display:flex;justify-content:space-between;align-items:center}.app--open .info__title{font-style:normal;font-weight:normal;font-size:10px;line-height:28px;letter-spacing:2px;text-transform:uppercase}.app--open .info__value{font-style:normal;font-weight:bold;font-size:20px;line-height:24px}.app--night{background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-nighttime.a9c730ff.jpg)}.app--night .info{color:var(--white);background:rgba(0,0,0,0.75);-webkit-backdrop-filter:blur(40.7742px);backdrop-filter:blur(40.7742px)}.clock{display:flex;flex-direction:column;justify-content:space-between;height:100%;padding:31px 26px 0 26px}.info{height:0vh;overflow:hidden;transition:0.1s}@media (min-width: 768px){.app{background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-daytime.bf5e0ad4.jpg)}.app--night{background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-nighttime.3c0b0370.jpg)}.app--open .info{box-sizing:border-box;flex-direction:row;padding:120px 165px 120px 65px}.app--open .info__section--left{margin-right:80px}.app--open .info__detail{flex-direction:column;align-items:flex-start;margin-bottom:48px}.app--open .info__title{margin:0;font-size:13px;line-height:28px;letter-spacing:2.6px}.app--open .info__value{margin:0;font-size:40px;line-height:48px}.clock{padding:80px 130px 65px 65px}}@media (min-width: 992px){.app{background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-daytime.22955f11.jpg)}.app--night{background-image:linear-gradient(to left, rgba(0,0,0,0.4), rgba(0,0,0,0.4)),url(/clock-app/static/media/bg-image-nighttime.aa5161f5.jpg)}.app--open .info{padding:74px 0px 74px 165px}.app--open .info__section--left{padding-right:147px;margin-right:94px;border-right:1px solid rgba(255,255,255,0.25)}.app--open .info__value{font-size:56px;line-height:68px}.clock{padding:56px 165px 98px 165px}.clock__action{display:flex;justify-content:space-between}}

.quote__content{width:90%;margin:0;font-style:normal;font-weight:normal;font-size:12px;line-height:22px}.quote__action{display:flex;justify-content:space-between}.btn--load{display:inline-block;background-image:url(/clock-app/static/media/icon-refresh.dd4042c8.svg);background-repeat:no-repeat;background-position:center;width:17px;height:17px}@media (min-width: 768px){.quote__content{font-size:18px;line-height:28px}.quote__author{font-style:normal;font-weight:bold;font-size:18px;line-height:28px}}@media (min-width: 992px){.quote{max-width:570px}}

.time__value{margin:16px 0;font-size:100px;line-height:100px;letter-spacing:-2.5px}.time__type{margin-left:15px;font-size:15px;line-height:28px;font-weight:400;text-transform:uppercase;letter-spacing:1px}.time__icon{display:inline-block;width:24px;height:24px;margin-right:26px}.time__title{margin:0;display:flex;align-items:center;font-weight:normal;font-size:15px;line-height:25px;letter-spacing:3px;text-transform:uppercase}.time__title--second{display:none}.time__location{margin:0;font-weight:bold;font-size:15px;line-height:28px;letter-spacing:3px;text-transform:uppercase}@media (min-width: 768px){.time__title{font-size:18px;line-height:28px;letter-spacing:3.6px}.time__title--second{display:inline;display:initial}.time__value{font-size:175px;line-height:175px;letter-spacing:-4.375px}.time__type{font-weight:300;font-size:32px;line-height:28px}.time__location{font-size:18px;letter-spacing:3.6px}}@media (min-width: 992px){.time__value{font-size:200px;line-height:200px}}

.btn--primary{display:flex;justify-content:space-between;align-items:center;margin-top:48px;margin-bottom:40px;padding-left:17px;width:115px;height:39px;border:none;outline:none;background-color:var(--white);border-radius:28px;font-style:normal;font-weight:bold;font-size:12px;line-height:14px;letter-spacing:3.5px;text-transform:uppercase;color:rgba(0,0,0,0.5)}.btn--primary>span{display:inline-block;width:32px;height:32px;border-radius:50%;background-color:var(--dark-gray);background-image:url(/clock-app/static/media/icon-arrow-up.9f27dbc9.svg);background-position:center;transform:rotate(180deg)}@media (min-width: 768px){.btn--primary{margin-top:80px;width:146px;height:56px;font-size:16px;line-height:28px;letter-spacing:6px;justify-content:space-around}.btn--primary>span{width:40px;height:40px}}@media (min-width: 992px){.btn--primary{margin:0;align-self:flex-end}}

